import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot} from '@angular/router';
import {BackendResponse} from '@common/core/types/backend-response';
import {AppHttpClient} from '@common/core/http/app-http-client.service';
import {catchError, mergeMap} from 'rxjs/operators';
import {EMPTY, of} from 'rxjs';
import {GetLinkGroupResponse, LinkGroupService} from '../../../shared/link/link-group.service';

@Injectable({
    providedIn: 'root'
})
export class LinkGroupShowResolverService implements Resolve<BackendResponse<GetLinkGroupResponse>> {
    constructor(
        private router: Router,
        private http: AppHttpClient,
        private linkGroup: LinkGroupService,
    ) {}

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): BackendResponse<GetLinkGroupResponse> {
        return this.linkGroup.get(route.params.groupId, route.queryParams).pipe(
            catchError(() => {
                this.router.navigate(['/dashboard/groups']);
                return EMPTY;
            }),
            mergeMap(response => {
                if (response) {
                    return of(response);
                } else {
                    this.router.navigate(['/dashboard/groups']);
                    return EMPTY;
                }
            })
        );
    }
}

